<template>
  <div >
    <ul class="his">
      <li v-for="item in arr">
        <div class="historyleft">
        <span></span>
      </div>
        <div class="historyright">
          <div class="hiszi">
            <p v-text="item.text"></p>
            <p><span v-text="item.money"></span></p>
          </div>
          <img :src="item.src" alt="">
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
  export default {
    data:function(){
      return {
        arr:[{
          text:'+++874...',
          money:'2900',
          src: require('../assets/images/img/history-lis0421_03.jpg')
        },{
          text:'6551...',
          money:'3969',
          src: require('../assets/images/img/history-lis0421_06.jpg')
        },{
          text:'++++++...',
          money:'3021',
          src: require('../assets/images/img/history-lis0421_08.jpg')
        },{
          text:'75A0 ...',
          money:'5596',
          src: require('../assets/images/img/history-lis0421_10.jpg')
        },{
          text:'7552+3...',
          money:'5963',
          src: require('../assets/images/img/history-lis0421_12.jpg')
        },{
          text:'++44 ...',
          money:'2966',
          src: require('../assets/images/img/history-lis0421_14.jpg')
        }]
      }
    }
  }
</script>
<style>
  .his > li{
    width: 100%;
    display: flex;
    flex-direction: row;
    padding: 1rem 0.5rem;
    box-sizing: border-box;
  }
  .his > li > .historyleft{
    width: 3rem;
    height: 3rem;
    background: blue;
    border-radius: 50%;
  }
  .his > li > .historyleft > span{
    width: 3rem;
    height: 3rem;
    display: block;
    background: url("../assets/images/icon/sprites.png") no-repeat -672px -247px;
  }
  .his > li > .historyright{
    width: 80%;
    background: #ccc;
    border-radius: 10px;
    margin-left: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 0.4rem;
    box-sizing: border-box;
  }
  .his > li > .historyright > img{
    width: 4rem;
    border-radius: 10px;
  }
  .his > li > .historyright > .hiszi{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  .his > li > .historyright > .hiszi > p{
    font-size: 0.8rem;
    color: #333;
  }
  .his > li > .historyright > .hiszi > p > span{
    color: #db9328;
  }
</style>



