<template>
  <div class="login">
    <Logintop text="">
      <router-link to="/registration" slot="slot"></router-link>
    </Logintop>
    <div class="log">
      <Loginname text="" place="//"></Loginname>
      <Loginname text="  " place=""></Loginname>
    </div>
  </div>
</template>
<script>
  import Logintop from './logintop.vue'
  import Loginname from './loginname.vue'
  export default {
    components:{
      Logintop,
      Loginname
    }
  }
</script>
<style scoped>
  .login {
    width: 100%;
  }
  .login > .log{
    width: 100%;
    margin-top: 1rem;
  }
</style>



