<template>
  <div class="hui">
    <ul>
      <li v-for="item in arr">
        <img :src="item.img" alt="">
        <p v-text="item.text"></p>
        <div>
          <p></p>
          <p><span v-text="item.money"></span></p>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
  export default {
    data:function(){
      return {
        arr:[{
          text:'8731...',
          img:require('../assets/images/img/item-img0329_01.jpg'),
          money:'1999'
        },{
          text:'753...',
          img:require('../assets/images/img/item-img0329_02.jpg'),
          money:'1999'
        },{
          text:'54...',
          img:require('../assets/images/img/item-img0329_03.jpg'),
          money:'1999'
        },{
          text:'664...',
          img:require('../assets/images/img/item-img0329_04.jpg'),
          money:'1999'
        },{
          text:'5+...',
          img:require('../assets/images/img/item-img0329_05.jpg'),
          money:'1999'
        },{
          text:'54...',
          img:require('../assets/images/img/item-img0329_06.jpg'),
          money:'1999'
        }]
      }
    }
  }
</script>
<style scoped>
  .hui,.hui > ul{
    width: 100%;
  }
  .hui > ul > li{
    width: 44%;
    float: left;
    border: 1px solid #ccc;
    margin: 0.5rem 2%;
  }
  .hui > ul > li > img{
    width: 100%;
  }
  .hui > ul > li:nth-child(odd){
    margin-left: 4%;
  }
  .hui > ul > li > div{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0.1rem 0.3rem;
    box-sizing: border-box;
  }
  .hui > ul > li > p{
    padding: 0.1rem 0.3rem;
    box-sizing: border-box;
    color: #333;
    font-size: 0.8rem;
  }
  .hui > ul > li > div > p{
    color: #333;
    font-size: 0.7rem;
  }
  .hui > ul > li > div > p > span {
    color: #f5c33c;
    margin-right: 0.1rem;
    font-size: 0.8rem;
  }
  .hui > ul > li > div > p:nth-of-type(2){
    font-size: 0.6rem;
  }
</style>



