<template>
  <div id="app">
    <router-view/>
    <!--<div class="sky"></div>-->
    <div class="foot">
      <router-link to="/home">
        <Footers text="" index="/home" @zhang="fn" :self="sel" >
          <img src="./assets/images/icon/footer_0322_05.png" slot="selected" alt="">
          <img src="./assets/images/icon/footer_0322_01.png" slot="uncheck" alt="">
        </Footers>
      </router-link>
      <router-link to="/destination">
        <Footers text="" index="/destination" @zhang="fn" :self="sel">
          <img src="./assets/images/icon/footer_0322_06.png" slot="selected" alt="">
          <img src="./assets/images/icon/footer_0322_02.png" slot="uncheck" alt="">
       </Footers>
      </router-link>
      <router-link to="/history">
        <Footers text="" index="/history" @zhang="fn" :self="sel">
          <img src="./assets/images/icon/footer_0322_07.png" slot="selected" alt="">
          <img src="./assets/images/icon/footer_0322_03.png" slot="uncheck" alt="">
       </Footers>
      </router-link>
      <router-link to="/individual">
        <Footers text="" index="/individual" @zhang="fn" :self="sel">
          <img src="./assets/images/icon/footer_0322_08.png" slot="selected" alt="">
          <img src="./assets/images/icon/footer_0322_04.png" slot="uncheck" alt="">
        </Footers>
      </router-link>
    </div>
  </div>
</template>

<script>
  import Footers from './components/footers.vue'
export default {
  name: 'app',
  components: {
    Footers
  },
  data:function(){
    return {
      sel:''
    }
  },
  methods:{
    fn:function(val){
      this.sel=val;
    }
  },
  created:function(){
    var d = window.location.hash.substr(1,5);
    if(d === '/' || d==='/home'){
      this.sel = '/home';
    }else if(d==='/dest'){
      this.sel = '/destination';
    }else if(d==='/hist'){
      this.sel = '/history';
    }else{
      this.sel = '/individual';
    }
  }
}
</script>

<style>
  *{
    margin: 0;
    padding: 0;
  }
  html{
    font-size: 20px;
  }
  ul{
    list-style: none;
  }
  a{
    text-decoration: none;
  }
  .foot{
    width: 100%;
    position: fixed;
    left: 0;
    bottom: 0;
    background: #ccc;
    height: 2.6rem;
    padding: 0.3rem 0;
  }
  .sky{
    width: 100%;
    height: 2.6rem;
  }

</style>



