<template>
  <div class="footer" @click="fn">
    <span v-show="flag"><slot name="uncheck"></slot></span>
    <span v-show="!flag"><slot name="selected"></slot></span>
    <p v-text="text" :class="{active:index==self}"></p>
  </div>
</template>
<script>
export default {
  props:['text','index','self'],
  methods:{
      fn:function(){
        this.$emit('zhang',this.index);
      }
  },
  computed:{
    flag:function(){
      if(this.index==this.self){
        return true;
      }
      return false;
    }
  }
}
</script>
<style>
  .footer{
    width: 25%;
    float: left;
    box-sizing: border-box;
    text-align: center;
  }
  .footer img {
    width: 30%;
  }
  .footer p{
    font-size: 0.8rem;
    color: #999;
  }
  .footer .active{
    color: blue;
  }
</style>



