<template>
  <div>
    <div class="des">
      <Top text="" smg=""></Top>
      <DesNav txt=""></DesNav>
    </div>
    <div class="desk"></div>
    <div class="left">
      <router-link to="/destination/hot" @click="fn" >
        <DesLeft text="" index="/destination/hot" @zhang="fn" :sel="select" >
          <img src="../assets/images/icon/sprites_03.png" slot="noSelected" alt="">
          <img src="../assets/images/icon/sprites_12.png" slot="Selected" alt="">
        </DesLeft>
      </router-link>
      <router-link to="/destination/domestic" @click="fn" >
        <DesLeft text="" index="/destination/domestic" @zhang="fn" :sel="select">
          <img src="../assets/images/icon/sprites_05.png" slot="noSelected" alt="">
          <img src="../assets/images/icon/sprites_13.png" slot="Selected" alt="">
        </DesLeft>
      </router-link>
      <router-link to="/destination/foreign" @click="fn" >
        <DesLeft text="" index="/destination/foreign" @zhang="fn" :sel="select">
          <img src="../assets/images/icon/sprites_07.png" slot="noSelected" alt="">
          <img src="../assets/images/icon/sprites_14.png" slot="Selected" alt="">
        </DesLeft>
      </router-link>
    </div>
    <div class="right">
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
  import DesNav from '../components/nav.vue'
  import DesLeft from '../components/desleft.vue'
  import Top from '../components/top.vue'
  export default {
    components:{
      DesNav,
      DesLeft,
      Top
    },
    data:function(){
      return {
        select:''
      }
    },
    methods:{
      fn:function(val){
        this.select=val;
      }
    },
    created:function(){
      var d = window.location.hash.substr(1);
      if(d === '/destination'){
        this.select = '/destination/hot';
      }else{
        this.select = d;
      }
    }
  }
</script>
<style scoped>
  .nav{
    margin-top: 2rem;
  }
  .des{
    width: 100%;
    height: 5rem;
    background: blue;
    position: fixed;
    top: 0;
    left: 0;
  }
  .desk{
    width: 100%;
    height: 5rem;
  }
  .left{
    width: 20%;
    height: 100%;
    float: left;
    position: fixed;
    top: 5rem;
    left: 0;
    background: #eaeaea;
  }
  .right{
    width: 80%;
    margin-left: 20%;
  }
  .left > a{
    width: 100%;
    z-index: 9;
  }
  .left  img{
    width: 100%;
    /*height: 3rem;*/
    margin: 0 auto;
    /*display: block;*/
    /*background: url("../assets/images/icon/sprites.png") no-repeat -444px -100px;*/
  }

  .left   p{
    text-align: center;
  }

</style>



