import Vue from 'vue'
import Router from 'vue-router'

//  
import Home from '../pages/home.vue'
import Destination from '../pages/destination.vue'
import Individual from '../pages/individual.vue'
import History from '../pages/history.vue'

//   
import Hot from '../destinations/hot.vue'
import Domestic from '../destinations/domestic.vue'
import Foreign from '../destinations/foreign.vue'


import Login from '../individual/login.vue'
import Registration from '../individual/registration.vue'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },{
      path: '/home',
      name: 'Home',
      component: Home
    },{
      path: '/destination',
      name: 'Destination',
      component: Destination,
      children:[
        {
          path: '',
          name: 'Hot',
          component: Hot
        },{
          path: 'hot',
          name: 'Hot',
          component: Hot
        },{
          path: 'domestic',
          name: 'Domestic',
          component: Domestic
        },{
          path: 'foreign',
          name: 'Foreign',
          component: Foreign
        }
      ]
    },{
      path: '/individual',
      name: 'Individual',
      component: Individual
      // children:[
      //   {
      //     path: 'login',
      //     name: 'Login',
      //     component: Login,
      //     children:[
      //       {
      //         path: 'registration',
      //         name: 'Registration',
      //         component: Registration
      //       }
      //     ]
      //   }
      // ]
    },{
      path: '/history',
      name: 'History',
      component: History
    }
  ]
})



