<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <div class="swiper-slide"><img src="../assets/images/img/banner1.jpg" alt=""></div>
      <div class="swiper-slide"><img src="../assets/images/img/banner2.jpg" alt=""></div>
      <div class="swiper-slide"><img src="../assets/images/img/banner3.jpg" alt=""></div>
      <div class="swiper-slide"><img src="../assets/images/img/banner4.jpg" alt=""></div>
    </div>
    <!--  -->
    <div class="swiper-pagination"></div>
  </div>
</template>
<script>
  import '../assets/Swiper-3.4.2/dist/js/swiper.min'
  export default {

    mounted:function(){
      new Swiper('.swiper-container',{
        pagination: '.swiper-pagination',
        speed:500,
        loop:true,
        autoplay: 2000
      })
    }
  }
</script>

<style>
  @import "../assets/Swiper-3.4.2/dist/css/swiper.min.css";
  .swiper-container{
    width: 100%;
  }
  .swiper-slide > img{
    width: 100%;
  }

</style>



