<template>
  <div class="home">
    <Navtop class="homenav" txt="//"></Navtop>
    <Banner></Banner>
    <Introduce></Introduce>
    <img src="../assets/images/img/ad_0328_02.jpg" class="home-img" alt="">
    <div class="sale">
      <span></span>
      <p></p>
    </div>
    <Hui></Hui>

  </div>
</template>
<script>
import Banner from '../components/banner.vue'
import Navtop from '../components/nav.vue'
import Introduce from '../components/introduce.vue'
import Hui from '../components/hui.vue'
  export default {
    components:{
      Banner,
      Navtop,
      Introduce,
      Hui
    }
  }
</script>
<style scoped>
  .home{
    width: 100%;
  }
  .homenav{
    width: 100%;
    height: 2.5rem;
    background: rgba(0, 0, 0, 0.3);
  }
  .home-img{
    width: 100%;
  }
  .home > .sale{
    padding: 0.5rem;
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .home > .sale > p{
    color: blue;
    margin-left: 0.5rem;
    font-weight: bold;
  }
  .home > .sale > span{
    width: 3rem;
    height: 3rem;
    display: inline-block;
    background: url("../assets/images/icon/sprites.png") no-repeat -369px -100px;
  }

</style>



