<template>
  <div class="logintop">
    <span></span>
    <p v-text="text"></p>
    <slot name="slot"></slot>
  </div>
</template>
<script>
  export default {
    props:['text']
  }
</script>
<style scoped>
  .logintop{
    width: 100%;
    height: 2.5rem;
    line-height: 2.5rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.5rem;
    box-sizing: border-box;
  }
  .logintop > span{
    width: 2rem;
    height: 2rem;
    display: block;
    background: url("../assets/images/icon/sprites.png") no-repeat -50px -30px;
    transform: scale(0.8);
  }
</style>



