<template>
  <div class="top">
    <img src="../assets/images/icon/arrow_03.png" alt="">
    <p v-text="text"></p>
    <p v-text="smg"></p>
  </div>
</template>
<script>
  export default {
    props:['text','smg']
  }
</script>
<style scoped>
  .top{
    width: 100%;
    height: 2.5rem;
    position: fixed;
    top: 0;
    left: 0;
    padding: 0 0.6rem;
    box-sizing: border-box;
    line-height: 2.5rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    color: #fff;
  }
  .top > p:nth-of-type(1){
    font-weight: bold;
  }
  .top > img{
    transform: rotate(180deg) scale(0.5);
  }
</style>



