<template>
  <div class="loginname">
    <p v-text="text"></p>
    <input type="text" :placeholder="place">
    <span></span>
  </div>
</template>
<script>
  export default {
    props:['text','place']
  }
</script>
<style scoped>
  .loginname{
    width: 90%;
    height: 2rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0 auto;
    box-sizing: border-box;
    align-items: center;
    border-bottom: 1px solid #999;
  }
  .loginname > input{
    outline: none;
    border: none;
    font-size: 1rem;
    color: #999;
    width: 60%;
  }
  .loginname > span{
    width: 1rem;
    height: 1rem;
    display: block;
    background: red;
  }
  .loginname > p{
    color: #666;
    width: 20%;
  }
  input::-ms-input-placeholder{
    color:#999;
  }
  input::-webkit-input-placeholder{
    color:#999;
  }
  input::-moz-placeholder{
    color:#999;
  }
</style>



