<template>
  <div class="desleft" @click="fn" :class="{desl:index==sel}">
    <span v-show="flag"><slot name="noSelected"></slot></span>
    <span v-show="!flag"><slot name="Selected"></slot></span>
    <p v-text="text" :class="{active:index==sel}"></p>
  </div>
</template>

<script>
  export default{
    computed:{
      flag:function(){
        if(this.index==this.sel){
          return false;
        }
        return true;
      }
    },
    props:['text','index','sel'],
    methods:{
      fn:function(){
        this.$emit('zhang',this.index);
      }
    }
  }
</script>

<style scoped>
  .desleft{
    width:100%;
    padding: 0.5rem 0;
    text-align:center;

  }
  .desl{
    border-left: 2px solid #F7C612;
    background: #fff;
  }
  .desleft > span {
    width: 100%;
    display: block;
    margin: 0 auto;
  }
  .desleft > p{
    color: #999;
  }
  .desleft .active{
    color: blue;
  }
</style>



