<template>
  <div class="nav">
    <div>
      <div><input type="text" :placeholder="txt"></div>
      <img src="../assets/images/icon/galss_03.png" alt="">
    </div>
  </div>
</template>
<script>
  export default {
    props:["txt"]
  }
</script>
<style scoped>
  .nav{
    width: 100%;
    height: 2.5rem;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9;
  }
  .nav > div{
    width: 90%;
    height: 2rem;
    position: relative;
    margin: 0.5rem auto;
  }
  .nav > div > div{
    width: 100%;
    height: 1.6rem;
    border-radius: 30px;
    border: 1px solid #fff;
  }
  .nav > div > div >input{
    width: 90%;
    border-radius: 30px;
    outline: none;
    border: none;
    background-color: transparent;
    text-indent: 2.1rem;
    color: #fff;
    font-size: 0.8rem;
    line-height: 1.6rem;
  }
  .nav > div > img{
    width: 1.3rem;
    position: absolute;
    top: 0.3rem;
    left: 0.4rem;
  }
  input::-ms-input-placeholder{
    color:#fff;
  }
  input::-webkit-input-placeholder{
    color:#fff;
  }
  input::-moz-placeholder{
    color:#fff;
  }

</style>



