<template>
  <div class="ind">
    <Top text="" ></Top>
    <div class="kong"></div>
    <div class="head">

          <div>
            <span></span>
          </div>

    </div>
    <div class="indiv">
      <div class="top">
        <h3> </h3>
        <p>~</p>
      </div>
      <div class="bottom">
        <div>
          <p><span></span>50</p>
          <p></p>
        </div>
        <div>
          <p>1000<span></span></p>
          <p></p>
        </div>
      </div>
    </div>
    <div class="order">
      <ul class="all">
        <li v-for="item in arr">
          <img :src="item.url" alt="">
          <p v-text="item.text"></p>
        </li>
      </ul>
      <ul class="mine">
        <li v-for="item in src">
          <div>
            <img :src="item.url" alt="">
            <p v-text="item.text"></p>
          </div>
          <div>
            <img src="../assets/images/icon/arrow_03.png" alt="">
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
  import Top from '../components/top.vue'
  import Login from '../individual/login.vue'
  export default {
    components:{
      Top,
      Login
    },
    data:function(){
      return {
        arr:[{
          text:'',
          url:require("../assets/images/icon/1.png")
        },{
          text:'',
          url:require("../assets/images/icon/2.png")
        },{
          text:'',
          url:require("../assets/images/icon/3.png")
        },{
          text:'',
          url:require("../assets/images/icon/4.png")
      }],
        src:[{
          text:'',
          url:require("../assets/images/icon/5.png")
        },{
        text:'',
          url:require("../assets/images/icon/6.png")
      },{
        text:'',
          url:require("../assets/images/icon/7.png")
      },{
        text:'',
          url:require("../assets/images/icon/8.png")
      }]
      }
    }
  }
</script>
<style scoped>
  .ind > .top{
    background: blue;
    z-index: 9;
  }
  .kong{
    width: 100%;
    height: 2.4rem;
  }
  .ind > .head{
    width: 100%;
    height: 4rem;
    background: blue;
    padding-top: 1rem;
  }
  .ind > .head > a > div{
    width: 6rem;
    height: 6rem;
    margin:  auto;
    background: #ccc;
    position: relative;
    border: 5px solid #fff;
    border-radius: 50%;
    z-index: 1;
  }
  .ind > .head > a > div > span{
    width: 6rem;
    height: 6rem;
    transform: scale(0.6);
    display: block;
    background: url("../assets/images/icon/sprites.png") no-repeat -502px -795px;
  }
  .ind > .indiv{
    width: 100%;
    background: #e5e5e5;
  }
  .ind > .indiv > .top{
    width: 100%;
    text-align: center;
    color: #666;
    padding-top: 3.5rem;
  }
  .ind > .indiv > .bottom{
    width: 100%;
    padding: 1rem 0;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    border-bottom: 1px solid #333;
  }
  .ind > .indiv > .bottom > div{
    text-align: center;
    color: #666;
    width: 49%;
  }
  .ind > .indiv > .bottom > div:nth-of-type(2){
    border-left: 1px dashed #333;
  }
  .ind > .order {
    width: 100%;
  }
  .ind > .order > .all {
    width: 100%;
    padding: 0.5rem;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
  .ind > .order > .all > li{
    width: 25%;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    color: #333;
    font-size: 0.8rem;
  }
  .ind > .order > .all > li > img{
    width: 3rem;
    margin: 0 auto;
  }
  .ind > .order > .mine{
    width: 100%;
    padding: 0.5rem 1.5rem;
    box-sizing: border-box;
  }
  .ind > .order > .mine > li {
    margin-top: 0.3rem;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    color: #333;
    font-size: 0.8rem;
  }
  .ind > .order > .mine > li > div{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .ind > .order > .mine > li > div > img{
    width: 3rem;
    height: 3rem;
  }
  .ind > .order > .mine > li > div:nth-of-type(2) > img{
    width: 1.2rem;
    height: 1.2rem;
  }
</style>



